<template>
  <div class="data-row-auto">
    <div class="auto-start">
      <p>Start at :</p>
      <input type="number" v-model="dataRowItemOption.start" class="ds-input" />
    </div>
    <div class="auto-increment">
      <p>Increment :</p>
      <input type="number" v-model="dataRowItemOption.increment" class="ds-input" />
    </div>
    <div class="auto-placeholder">
      <p>placeholder string :</p>
      <input type="text" v-model="dataRowItemOption.placeholder" class="ds-input" />
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow
      ? computed(() => props.dataRow.item.itemOption)
      : ref({
          start: 1,
          increment: 1,
          placeholder: '',
        });

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

